<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col :span="8">
          <el-input placeholder="">
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="ordersList" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column
          width="230px"
          label=""
          prop="order_number"
        ></el-table-column>
        <el-table-column label="" prop="order_price"></el-table-column>
        <el-table-column label="" prop="pay_status">
          <template slot-scope="scope">
            <el-tag type="success" v-if="scope.row.pay_statue === '1'"
              ></el-tag
            >
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="is_send"></el-table-column>
        <el-table-column label="" prop="create_time">
          <template slot-scope="scope">
            {{ scope.row.create_time | dateFormat }}
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              @click="showDialogEditAddress"
            ></el-button>
            <!--  -->
            <el-button
              size="mini"
              type="success"
              icon="el-icon-location"
              @click="showProgressDialog"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
      @close="closeDialog"
      title=""
      :visible.sync="editAddressVisible"
      width="50%"
    >
      <!--  -->
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        label-width="100px"
      >
        <el-form-item label="/" prop="address1">
          <el-cascader
            :options="citydata"
            v-model="addForm.address1"
          ></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="addForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editAddressVisible = false"> </el-button>
        <el-button type="primary" @click="editAddressVisible = false"
          > </el-button
        >
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="progressVisible" width="50%">
      <!-- timeline -->
      <el-timeline>
        <el-timeline-item
          v-for="(item, index) in progressInfo"
          :key="index"
          :timestamp="item.time"
          :icon="item.icon"
          :type="item.type"
          :color="item.color"
          :size="item.size"
        >
          {{ item.status }}
        </el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>
<script>
// /
import citydata from './citydata.js'
export default {
  data() {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      // 
      ordersList: [],
      // 
      total: 0,
      // 
      editAddressVisible: false,
      // 
      addForm: {
        address1: [],
        address2: ''
      },
      // 
      citydata: citydata,
      // 
      progressVisible: false,
      // 
      progressInfo: [],

      // apiKey
      apiKey: '74eVcQWe17a59e7cc73f98a52195327273b3870dcd26d94',
      // 
      number: '75374604843016',
      // 
      icon: [
        'el-icon-more',
        'el-icon-star-off',
        'el-icon-s-flag',
        'el-icon-s-comment',
        'el-icon-s-promotion',
        'el-icon-collection-tag'
      ],
      // type
      type: ['success', 'error', 'danger', 'warning', 'info'],
      // 
      color: ['pinke', 'red', 'green', 'blue', 'skyblue'],
      size: 'large',
      // 
      addFormRules: {
        address1: [
          { required: true, message: '', trigger: 'blur' }
        ],
        address2: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    this.getOrdersList()
  },
  methods: {
    async getOrdersList() {
      const { data: res } = await this.$http.get('orders', {
        params: this.queryInfo
      })
      // 
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      // 
      console.log(res)
      // 
      this.ordersList = res.data.goods
      this.total = res.data.total
    },
    // 
    handleSizeChange(newPageSize) {
      this.queryInfo.pagesize = newPageSize
      // 
      this.getOrdersList()
    },
    // 
    handleCurrentChange(newPagenum) {
      this.queryInfo.pagenum = newPagenum
      // 
      this.getOrdersList()
    },
    // 
    showDialogEditAddress() {
      // 
      this.editAddressVisible = true
    },
    // 
    closeDialog() {
      // 
      this.$refs.addFormRef.resetFields()
    },
    // 
    async showProgressDialog() {
      // 
      this.progressVisible = true
      // 
      const res = await fetch(
        `https://api.apishop.net/common/express/getExpressInfo?apiKey=${this.apiKey}&expressNumber=${this.number}`
      ).catch(err => err)
      const result = await res.json()
      // .then(data => {
      //   this.progressInfo = data.result.list
      //   console.log(this.progressInfo)
      // })
      // 
      if (result.statusCode !== '000000') {
        this.$message.error('')
      }
      // 
      this.progressInfo = result.result.list
      this.progressInfo.forEach(item => {
        item.color = this.color[this.getRandom(0, this.color.length - 1)]
        item.icon = this.icon[this.getRandom(0, this.icon.length - 1)]
        item.type = this.type[this.getRandom(0, this.type.length - 1)]
        item.size = this.size
      })
      console.log(this.progressInfo)
    },
    // 
    getRandom(min, max) {
      return Math.floor(Math.random() * (max - min + 1) + min)
    }
  }
}
</script>
<style lang="less" scoped>
// @import '../../plugins/timeline/timeline.css';
// @import '../../plugins/timeline-item/timeline-item.css';
</style>
